import React, { useEffect, useState, useRef,Fragment } from "react";
import { Button, Container, Form, Row, Col } from 'react-bootstrap';
import axios from "axios";
import { baseURL } from "./utils/constant";
import {Table,Pagination } from "react-bootstrap";
function AddReceipt1(){
  const [rowsData, setRowsData] = useState([]);

  const addTableRows = ()=>{

      const rowsInput={
          fullName:'',
          emailAddress:'',
          salary:''  
      } 
      setRowsData([...rowsData, rowsInput])
    
  }
 const deleteTableRows = (index)=>{
      const rows = [...rowsData];
      rows.splice(index, 1);
      setRowsData(rows);
 }

 const handleChange = (index, evnt)=>{
  
  const { name, value } = evnt.target;
  const rowsInput = [...rowsData];
  rowsInput[index][name] = value;
  setRowsData(rowsInput);



}
  return(
    <Fragment>
              <Table className="table table-striped table-primary table-hover text-center  table-bordered border-dark" size="sm">
                  <thead>
                    <tr>
                        <th></th>
                        <th>Email Address</th>
                        <th>Salary</th>
                        <th><button className="btn btn-outline-success" onClick={addTableRows} >+</button></th>
                    </tr>
                  </thead>
                 <tbody>
                 <TableRows rowsData={rowsData} deleteTableRows={deleteTableRows} handleChange={handleChange} />
                 </tbody> 
              </Table>
            </Fragment>
              
  )
}
function TableRows({rowsData, deleteTableRows, handleChange}) {
  return(
      
      rowsData.map((data, index)=>{
          const {fullName, emailAddress, salary}= data;
          return(
              <tr key={index}>
              <td>
             <input type="text" value={fullName} onChange={(evnt)=>(handleChange(index, evnt))} name="fullName" className="form-control"/>
              </td>
              <td><input type="text" value={emailAddress}  onChange={(evnt)=>(handleChange(index, evnt))} name="emailAddress" className="form-control"/> </td>
              <td><input type="text" value={salary}  onChange={(evnt)=>(handleChange(index, evnt))} name="salary" className="form-control" /> </td>
              <td><button className="btn btn-outline-danger" onClick={()=>(deleteTableRows(index))}>x</button></td>
          </tr>
          )
      })
 
  )
  
}
export default AddReceipt1;